* {
  padding: 0;
  margin: 0;
}

.MainDiv {

  padding: 1cm 5vw;
}

h1 {
  font-weight: 500;
  font-size: 22px;
}

.main-div-box {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.cards {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;

  margin-top: 1cm;
  gap: .5cm;
}

.name {
  border-bottom: thin solid rgb(201, 201, 201);
  padding: .05cm 0cm;
  word-break: break-all;
}

.bold-name {
  font-weight: 600;
}

.card {
  /* border: 1px solid black; */
  max-width: 8cm;
  border: 1px solid rgb(235, 212, 255);
  background-color: rgb(243, 229, 255);
  width: fit-content;
  padding: .15cm .3cm;
  border-radius: 5px;
  box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;

}

.del-box {
  padding: 0cm 0cm .2cm 0cm;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.call-box {
  padding: 0.2cm 0cm 0cm 0cm;
}

.delete {
  padding: .1cm .3cm;
  border: 1px solid rgb(255, 185, 185);
  border-radius: 5px;
  background-color: rgb(255, 185, 185);
  cursor: pointer;
  transition: .3s;
  width: 100%;
  text-align: center;

}

.delete:hover {
  background-color: rgb(255, 148, 148);
  border: 1px solid rgb(255, 148, 148);
  /* color: white; */
}

.call {
  padding: .1cm .3cm;
  border: 1px solid forestgreen;
  border-radius: 5px;
  background-color: forestgreen;
  cursor: pointer;
  transition: .3s;
  width: 100%;
  color: white;
  text-align: center;

}

.call:hover {
  background-color: rgb(13, 121, 13);
  border: 1px solid rgb(13, 121, 13);
  /* color: white; */
}

.navImg {
  width: 3.5cm;
}

.nav-box {
  margin-bottom: 1cm;
}

@media only screen and (max-width : 600px) {
  h1 {
    font-size: 20px;
    text-align: center;
  }

  .card {
    width: 100%;
    padding: .15cm .2cm;
  }

  .navImg {
    width: 3cm;
  }
}

@media only screen and (max-width : 500px) {
  h1 {
    font-size: 18px;
    text-align: center;
  }

  .name,
  .call,
  .delete {
    font-size: 15px;
  }
}